.section__ourHistory {
    display: grid;
    place-items: center;
    width: 100%;
    min-height: 433px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.ourHistory__description {
    max-width: 820px;
    text-align: center;
}

.ourHistory__description__heading {
    padding-bottom: 71px;
}

.button {
    width: 163px;
}



@media (max-width:768px) {

    .section__ourHistory {
        background-position: right;
    }

    .ourHistory__description {
        padding: 0 38px;
    }

    .ourHistory__description__heading {
        font-size: 22px;
        padding-bottom: 29px;
    }
}